export default function replaceSpecialChars (str) {
    if (!str) return
    return str.replace(/[]+/g, 'a')
        .replace(/[]+/g, 'o')
        .replace(/+/g, 'c')
        .replace(/+/g, 's')
        .replace(/+/g, 'e')
        .replace(/+/g, 'l')
        .replace(/+/g, 'n')
        .replace(/[]+/g, 'z')
}
